.main-offer {
  display: flex;
  justify-content: space-between;
  height: 600px;
  margin-top: 150px;
  background-color: #2e90e5;
}

.main-offer .offer-description {
  width: 395px;
  padding-top: 161px;
  margin-left: 70px;
  color: white;
}

.main-offer .offer-title {
  font-size: 66px;
  font-weight: 300;
}

.main-offer .offer-text {
  margin: 40px 0;
  line-height: 22px;
  font-size: 24px;
}

.main-offer .offer-image {
  margin-top: -109px;
  margin-right: -40px;
  height: 709px;
}

.main-offer .offer-link {
  text-decoration: none;
  line-height: 22px;
  font-size: 14px;
  color: white;
  border-bottom: 3px solid white;
}

@media (max-width: 1023px) {

  .main-offer .offer-description {
    width: 100%;
    padding-top: 200px;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 30px;
    margin: 0;
  }

  .main-offer {
    position: relative;
    height: min-content;
  }

  .main-offer .offer-title {
    font-size: 32px;
  }

  .main-offer .offer-text {
    font-size: 16px;
  }

  .main-offer .offer-image {
    position: absolute;
    top: 0;
    margin-top: -50px;
    width: 100%;
    height: 200px;
  }

}